*, 
*:before, 
*:after {
    box-sizing: border-box;
}

body {
    font-family: 'Nunito Sans';
    margin: 0;
    padding: 0;
    display: flex;
    flex-direction: column;
    justify-content: center;
}

:root {
    --darkBlueElements: hsl(209, 23%, 22%);
    --veryDarkBlueBg: hsl(207, 26%, 17%);
    --veryDarkBlueText: hsl(200, 15%, 8%);
    --darkGrayInput: hsl(0, 0%, 52%);
    --veryLightGrayBg: hsl(0, 0%, 98%);
    --whiteTextElements: hsl(0, 0%, 100%);
}

.navbar-light,
.navbar-dark {
    display: flex;
    justify-content: space-between;
    box-shadow: 0px 6px 5px -2px rgba(0, 0, 0, 0.118);
    padding: 1rem 5%;
}

.navbar-dark {
    background-color: var(--darkBlueElements);
    color: var(--whiteTextElements);
}

.navbar__darkmode {
    display: flex;
    align-items: center;
    cursor: pointer;
}

.navbar__darkmodeIcon {
    margin-right: 10px;
}

#navbar__logo {
    font-weight: 800;
}

#darkmode__text {
    font-weight: 300;
    margin: 0;
}

section {
    display: flex;
    flex-direction: column;
    justify-content: center;
}
.search-light,
.search-dark {
    margin: 2rem;
    padding: 1rem 2rem;
    border-radius: 5px;
    display: flex;
    align-items: center;
    box-shadow: 0px 0px 10px -2px rgba(0, 0, 0, 0.194);

}

.search-dark {
  background-color: hsl(209, 23%, 22%);
}

.search-light i,
.search-dark i {
    margin-right: 1.5rem;
    color: var(--darkGrayInput);
}

#search__text {
    font-size: large;
    border: none;
    background-color: transparent;
}

#search__text:focus {
    outline: none;
}

.search-drop-down-light,
.search-drop-down-dark {
    display: flex;
    flex-direction: column;
}
.drop-down-light,
.drop-down-dark {
    display: inline-block;
    appearance: none;
    border-radius: 7px;
    width: 50%;
    max-width: 300px;
    padding: 1rem 2rem;
    border: none;
    box-shadow: 0px 0px 10px -2px rgba(0, 0, 0, 0.194);
    cursor: pointer;
    margin-left: 1.9rem; 
    margin-bottom: 3rem;
    position: relative;
    /* bottom: 0; */
}
.drop-down-dark {   
    background-color: var(--darkBlueElements);
    color: white;
}

.drop-down__values-light,
.drop-down__values-dark {
    background-color: var(--veryLightGrayBg);
    display: none;
    position: absolute;
    z-index: 1;
    max-width: 300px;
    margin-top: .4rem;
    border-radius: 7px;
    box-shadow: 0px 0px 10px -2px rgba(0, 0, 0, 0.194);
    width: 50%;
    top: 260px;
    left: 30px;
}
.drop-down__values-dark {
    background-color: var(--darkBlueElements);
    color: white;
}
.expand {
    float: right;
}
.drop-options {
    margin: 0;
    cursor: pointer;
    padding: 1.7rem 2rem;
}
.drop-options:hover {
    cursor: pointer;
    color: white;
    background-color: rgba(0, 0, 255, 0.769);
}
.drop-options:hover:first-child {
    border-top-left-radius: 7px;
    border-top-right-radius: 7px;

}

.drop-options:hover:last-child {
    border-bottom-left-radius: 7px;
    border-bottom-right-radius: 7px;

}
.countries {
    display: flex;
    flex-direction: column;
    flex: 1 0 21%;
}
.country-card-light,
.country-card-dark {
    margin: 0 auto;
    padding: 0;
    display: flex;
    flex-direction: column;
    justify-content: center;
    
    width: 300px;
    max-width: 600px;
    border-radius: 13px;
    box-shadow: 0px 0px 10px -2px rgba(0, 0, 0, 0.194);
    padding-bottom: 4rem;
    margin-bottom: 4rem;
}

.country-card-dark {
  color: white;
  background-color: hsl(209, 23%, 22%);
}
.country-card-light h3,
.country-card-dark h3 {
    margin-top: 2rem;
    margin-left: 2rem;
    margin-right: 2rem;;
}

.country-card__flag,
.country-card__flag{
    border-top-right-radius: 13px;
    border-top-left-radius: 13px;
    margin: 0;
    width: 100%;
    object-fit: contain;
}

.country-card-light p,
.country-card-dark p {
    padding: 0;
    margin: .2rem 2rem;
    margin-right: auto;
}

@media (min-width: 1440px) {
    .countries {
        flex-direction: row;
        justify-content: space-between;
        flex-wrap: wrap;
    }
    
    
}

.back-to-home-light,
.back-to-home-dark {
    align-self: flex-start;
    border-radius: 7px;
    margin-left: 5%;
    width: 130px;
    font-size: large;
    background-color: var(--whiteTextElements);
    border: none;
    padding: 1rem 2.6rem;
    box-shadow: 0px 0px 10px -2px rgba(0, 0, 0, 0.194);
    margin-bottom: 2rem;
    margin-top: 2rem;
}

.back-to-home-dark {
    background-color: var(--darkBlueElements);
    color: white;
}

.link-dark:visited {
    color: var(--whiteTextElements)
}

.link-light:visited {
    color: var(--veryDarkBlueText);
}

.country-detail-container {
    text-align: left;    
    display: flex;
    flex-direction: column;
    justify-content: center;
    margin-left: 5%;
}
.country-detail-light,
.country-detail-dark {
    font-size: 1.3rem;
    text-align: left;
    padding: 1rem 0;
}

.country-detail-dark {
    color: var(--whiteTextElements)
}
.country-detail-name {
    font-size: 2rem;
}

.country-detail-flag {
    border-radius: none;
    width: 90%;
}

.country-detail-sub-container {
    display: flex;
    flex-direction: column;
}

.country-detail-sub {
    margin-bottom: 1rem;
}

.borders-container {
    align-self: flex-start;
    text-align: center;
    display: flex;
    flex-direction: column;
    flex-wrap: wrap;
    margin-bottom: 5%;
}

.borders-light,
.borders-dark {
    font-weight: 300;
    margin-right: 1em;
    margin-bottom: 1em;
    width: 160px;
    text-align: center;
    padding: 1rem 1.2rem;
    background-color: var(--veryLightGrayBg);
    box-shadow: 0px 0px 10px -2px rgba(0, 0, 0, 0.194);
}

.borders-dark {
    background-color: var(--darkBlueElements);
    color: var(--whiteTextElements)
}
.country-detail-name {
    font-weight: 600;
}

.borders-boxes {
    align-items: center;
    display: flex;
    flex-wrap: wrap;
}


@media (min-width: 1000px) {

    .country-detail-dark,
    .country-detail-light {
        font-size: 1rem;
    }
    .country-detail-container {
        flex-direction: row;
        justify-content: space-between;

        margin: 5%;
    }
    .detail {
        margin: 0;
    }
    .country-detail-sub-container {
        flex-direction: row;
        margin-bottom: 2rem;
    }

    .country-detail-flag {
        max-width: 850px;
        margin-left: 0;
    }

    .country-detail-name {
        margin: 0;
        padding: 0;
    }

    .detail {
        display: flex;
        flex-direction: column;
        padding: 0;
        margin: 0;
    }

    .borders-container {
        flex-direction: row;
    }
    .country-detail-right {
        margin-left: 2rem;
    }
    .borders-boxes {
        align-items: center;
        display: flex;
        flex-wrap: wrap;
    }
}